
import { defineComponent } from "vue";
import Pagination from '@/shared/components/Pagination/Pagination.vue'
import { PAGE_SIZE } from "@/shared/constants/common.const";
import type { CommonCodeList, COM21100111_RES } from "@/shared/type/COM2111001.type";
import BreadCrumb from '@/shared/components/BreadCrumb/BreadCrumb.vue';

export default defineComponent({
    name: "com211001sc00",
    components: {
        Pagination,
        BreadCrumb
    },
    data() {
        const header = [
            { columnName: this.$t('com211001sc00.column.no') },
            { columnName: this.$t('com211001sc00.column.view') },
            { columnName: this.$t('com211001sc00.column.value_of_code') },
            { columnName: this.$t('com211001sc00.column.code_name') },
            { columnName: this.$t('com211001sc00.column.column_name') },
            { columnName: this.$t('com211001sc00.column.status_code') },
            { columnName: this.$t('com211001sc00.column.standard_code') },
            { columnName: this.$t('com211001sc00.column.data_type') },
            { columnName: this.$t('com211001sc00.column.data_length') },
            { columnName: this.$t('com211001sc00.column.description') },
        ] as { columnName: string }[]
        const statusCode = [
            { key: '01', value: 'Active' },
            { key: '02', value: 'Inactive' },
        ] as { key: string, value: string }[]
        const searchForm = {
            standardCodeDomainNo: "",
            standardCodeKorDomainNo: "",
            businessCommonStatusCode: "",
        } as { standardCodeDomainNo: string, standardCodeKorDomainNo: string, businessCommonStatusCode: string }
        const commonCodeList = [] as CommonCodeList[]
        const isReset = false as boolean
        const startingIndex = 1 as number
        const currentPage = 1 as number
        const totalItems = 0 as number
        const pageSize = PAGE_SIZE as number

        return {
            header,
            commonCodeList,
            statusCode,
            searchForm,
            totalItems,
            currentPage,
            pageSize,
            startingIndex,
            isReset
        };
    },

    mounted() {
        this.requestCommonCodeList();
    },

    computed: {
        covertCodeValue() {
            return (covertCodeType: string, baseCode: string) => `${baseCode}` + ' : ' + this.$codeConverter.convertValue(`${covertCodeType}`, `${baseCode}`);
        }
    },

    methods: {
        async requestCommonCodeList() {
            const reqBody = {
                pageNumber: this.currentPage,
                pageSize: this.pageSize,
                businessCommonStatusCode: this.searchForm.businessCommonStatusCode,
                standardCodeKorDomainNo: this.searchForm.standardCodeKorDomainNo,
                standardCodeDomainNo: this.searchForm.standardCodeDomainNo,
            };

            const res = await this.$request.getRequest("COM21100111", reqBody, false) as COM21100111_RES;
            this.commonCodeList = res?.body.list.map((item: CommonCodeList) => {
                return {
                    standardCodeKorDomainNo: item.standardCodeKorDomainNo,
                    standardCodeDomainNo: item.standardCodeDomainNo,
                    businessCommonStatusCode: this.covertCodeValue("BusinessCommonStatusCode", item.businessCommonStatusCode),
                    standardCodeDomainDesc: item.standardCodeDomainDesc,
                    standardCodeDomainName: item.standardCodeDomainName,
                    codeDataLengthContent: item.codeDataLengthContent,
                    codeDataTypeContent: item.codeDataTypeContent,
                    standardCodeValue: item.standardCodeValue,
                    standardCodeValueDesc: item.standardCodeValueDesc
                }
            });
            this.totalItems = res.body.totalCount;
        },

        handleView(item: CommonCodeList) {
            this.$router.push({
                path: `COM211001SC01`,
                query: {
                    type: "edit",
                    standardCodeDomainName: item.standardCodeDomainName
                }
            })
        },

        handleValueOfCode(item: CommonCodeList) {
            this.$router.push(
                {
                    path: `COM211001SC02`,
                    query: {
                        standardCodeDomainName: item.standardCodeDomainName,
                        codeDataLengthContent: item.codeDataLengthContent,
                        standardCodeDomainNo: item.standardCodeDomainNo,
                    }
                }
            );
        },

        handleActionClick(actionType: string) {
            switch (actionType) {
                case 'register':
                    this.$router.push({
                        path: `COM211001SC01`,
                        query: { type: "register" }
                    });
                    break;
                case 'search':
                    if (this.currentPage > 1) {
                        this.currentPage = 1
                        this.isReset = true
                        this.requestCommonCodeList()
                    } else {
                        this.requestCommonCodeList()
                    }
                    break;
                default: //reset
                    this.isReset = true
                    this.currentPage = 1
                    this.searchForm.standardCodeKorDomainNo = ""
                    this.searchForm.standardCodeDomainNo = ""
                    this.searchForm.businessCommonStatusCode = ""
                    this.requestCommonCodeList()
                    break;
            }
        },
        handlePagination(pageNumber: number) {
            this.isReset = false
            this.currentPage = pageNumber
            this.requestCommonCodeList()
        },

        handleStartingIndex(startingIndex: number) {
            this.startingIndex = startingIndex
        }
    },
});
